*{
    margin: 0;
    padding: 0;
    color: #333;
    text-decoration: none;
    list-style: none;
    font-size: 14px;
}
h2{
    font-size: 20px;
}
.fl{float: left;}

.fr{float: right;}

.nav_Left{
    position: relative;
}

.nav_Left ul{
    width: 200px;
    border-bottom: 1px solid #ccc;
    padding: 20px 30px;
}
.nav_Left ul li{
    text-align: left;
    height: 40px;
    line-height: 40px;
}

.nav_Left ul li link{
    color: #333;
    text-decoration: none;
}

.topBar_right ul li{
    display: inline-block;
    padding: 15px 30px;
}

.rule{
    font-size: 16px;
    height: 35px;
    line-height: 35px;
    font-weight: 700;
    text-indent:-1em; 
}

.content{
    position: absolute;
    top: 30px;
    left: 280px;
}


